header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}


/* RESUME */
.resume {
    right: -2.3rem;
    
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

.header__socials {
    margin-top: 1.5rem;
    display: flex;
    gap: 1.5rem;
    justify-content: center;

}

/* LOGOS */
.socials__linkedin{
    color: var(--color-white);
    transition: var(--transition);
}

.socials__linkedin:hover{
    color: #0072b1;
    transform: scale(2);
}

.socials__github{
    color: var(--color-white);
    transition: var(--transition);
}

.socials__github:hover{
    color: #111;
    transform: scale(2);
}

.socials__instagram{
    color: var(--color-white);
    transition: var(--transition);
}

.socials__instagram:hover{
    color: #d62976;
    transform: scale(2);
}

/* PRO PIC */

.pic {
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    
    border-radius: 40rem 40rem 0rem 0rem;
    margin-top: 4rem;
    overflow: hidden;
    
}